@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 60vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Layout-body {
    /*background-color: #6B8EE5;*/
    background-color: #191919;
    background-image: url(/jrs-website/static/media/background.9ae318fa.jpg);
    background-repeat: no-repeat;
    background-position: center top;
    min-height: 100vh;
    padding: 1.7em 0.2em 1.7em 1.7em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(5px + 2vmin);
    color: gray;
    list-style: none;
}

.Layout-header {
    background-color: #D9D8DF;
    top: 0;
    width: 100%; /* Full width */
}

.Layout-footer {
    display: flex;
    background-color: #D9D8DF;
    width: 100%; /* Full width */
}

.Layout-link {
    color: #61dafb;
}

.Nav-right {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
}

.Nav-right a {
    padding: 1em;
    color: darkslategrey;
    text-decoration: none;
    font-weight: bolder;
}

.Nav-right a:hover {
    background-color: white;
}

.Standout-text {
    /*color: #FFE667;*/
    color: #CCA316;
}

.Resume-bullets {
    display: inherit;
    align-items: center;
    justify-content: center;
}

.Resume-bullets li {
    list-style-position: inside;
    width: 60%;
    margin: auto;
    text-align: center;
}

.School-information {
    display: inherit;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.Resume-title {
    text-decoration: underline;
    margin: 0;
    padding: 0;
    display: inherit;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.Resume-body {
    width: 100%;
    display: flex;
    flex-direction: column;
}
